IACrB,kBAAkB;IAClB,kBAAkB;IAClB,qBAAqB;IACrB,wBAAwB;IACxB,SAAS;IACT,UAAU;IACV,UAAU;AACd;AAEA;IACI,mBAAmB;IACnB,sCAAsC;IACtC,UAAU;AACd;AAEA;IACI,aAAa;IACb,oBAAoB;AACxB;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,cAAc;IACd,mBAAmB;IACnB,aAAa;IACb,mBAAmB;IACnB,iBAAiB;IACjB,kBAAkB;IAClB,YAAY;AAChB","sourcesContent":["<template>\n    <div class=\"container\">\n        <div class=\"name-div\">\n            <label class=\"name-label\">Name</label>\n            <IDEInput elementId=\"nameInput\" @getValue=\"setNameValue\" :type=\"'text'\" :value=\"name\" info-text=\"\"\n                :validations=\"validations.name\" @validateStatus=\"setNameValidationStatus\"\n                placeholder=\"Give your theme a name.\" />\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close\">Cancel</button>\n            <button class=\"create-button\" :disabled=\"createButtonDisabled\" @click=\"save\">SAVE</button>\n            <!-- <span class=\"button-tooltip\" style=\"top: 160px; left: 366px; z-index: 1; width: 151px;\">Organization name must have at least 3 characters.</span> -->\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { computed, ref } from \"vue\";\nimport { IIDEInputDefinitions } from \"../../../../domain/presentation/IIDEInputDefinitions\";\nimport IDEInput from \"../application/IDEInput.vue\";\n\nconst emit = defineEmits([\"close\"]);\nconst props = defineProps<{ name: string }>();\nconst name = ref(props.name);\nconst validations = ref<Record<\"name\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 3, max: 60 }, nonSpaceCharacter: true, customRegex: { regex: /^([a-zA-Z0-9-_]+)$/, message: \"Available characters: A-Z, a-z, 1-9, '-' and '_'\" } }\n});\nconst isNameValid = ref<boolean>(false);\nasync function save() {\n    emit(\"close\", name.value);\n}\nfunction setNameValue(value: string) {\n    name.value = value;\n}\n\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n\nfunction close() {\n    emit(\"close\");\n}\nconst createButtonDisabled = computed(() => !isNameValid.value || name.value.length == 0);\n</script>\n<style scoped>\n.container {\n    padding: 32px 20px 20px;\n}\n\n.name-div {\n    margin-bottom: 52px\n}\n\n.cancel-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #757575;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    margin-right: 4px;\n    border: none;\n    background: transparent;\n}\n\n.create-button:disabled {\n    color: #757575\n}\n\n.name-label {\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    color: #151233;\n    margin-bottom: 4px;\n}\n\n.button-tooltip {\n    visibility: hidden;\n    position: absolute;\n    background: #757575;\n    color: #FFFFFF;\n    font-size: 11px;\n    font-family: 'Roboto';\n    text-align: center;\n    border-radius: 4px;\n    letter-spacing: 0.2px;\n    padding: 6px 8px 6px 8px;\n    top: 56px;\n    left: 63px;\n    opacity: 0;\n}\n\n.create-button:disabled:hover+.button-tooltip {\n    visibility: visible;\n    transition: opacity 0.3s ease-out 0.5s;\n    opacity: 1;\n}\n\n.buttons-div {\n    display: flex;\n    justify-content: end;\n}\n\n.create-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #FFFFFF;\n    background: #151233;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: none;\n}\n</style>\n"],"sourceRoot":""}]);
